<template>
  <div class="orderDetails">
    <div class="header">
      <div class="orderDetailsHeader">
        <div class="headerContent">
          <van-icon name="arrow-left" @click="$router.go(-1)" />
          <span>{{ $t("index.text120") }}</span>
        </div>
      </div>
      <div class="slot"></div>
    </div>

    <div class="orderDetailsMain">
      <div class="state jcsb">
        <div class="stateLeft" v-if="orderInfo.status == 0">
          {{ $t("index.text121") }}
        </div>

        <div class="stateLeft" v-if="orderInfo.status == 1">
          {{ $t("index.text122") }}
        </div>

        <div
          class="stateLeft"
          v-if="
            orderInfo.status == 2 && type == 0 && orderInfo.is_evaluate == 0
          "
        >
          {{ $t("index.text123") }}
        </div>

        <div class="stateLeft" v-if="orderInfo.status == 3 && type == 0">
          {{ $t("index.text124") }}
        </div>

        <div
          class="stateLeft"
          v-if="orderInfo.status == 2 && orderInfo.is_evaluate == 1"
        >
          {{ $t("index.text125") }}
        </div>
      </div>

      <div class="logisticsNum mt2r" v-if="orderInfo.shippingNo">
        <div class="num jcsb">
          <div style="display: flex; align-items: center">
            <img src="../../assets/img/pic14.png" alt="" />
            <span style="font-weight: bold"> {{ $t("index.text126") }}</span>
          </div>
          <div
            style="color: #d12c21"
            v-clipboard:copy="orderInfo.shippingNo"
            v-clipboard:success="fzdzhiAs"
          >
            {{ $t("index.text127") }}
          </div>
        </div>

        <div class="mt2r">{{ orderInfo.shippingNo }}</div>
      </div>

      <div class="receivingAddress mt2r">
        <div>
          <div class="userInfo jcsb">
            <span>
              <img
                src="../../assets/img/navBar2.png"
                class="navBar2"
                alt=""
              />{{
                orderInfo.address_text && orderInfo.address_text.name
              }}</span
            >
            <span>{{
              orderInfo.address_text && orderInfo.address_text.phone
            }}</span>
          </div>

          <div class="userAddress mt2r">
            {{ orderInfo.address_text && orderInfo.address_text.address }}
          </div>
        </div>
      </div>

      <div class="orderItem" @click="$router.push('/orderDetails')">
        <div class="orderNum jcsb">
          <span>{{ orderInfo.shopInfo && orderInfo.shopInfo.shop_name }}</span>
        </div>
        <div class="orderContent">
          <img :src="$url + orderInfo.image" class="left" alt="" />
          <div class="right">
            <div class="textInfo">
              {{ orderInfo.title }}
            </div>

            <div class="price">{{ orderInfo.price }}HF</div>
          </div>
        </div>

        <div class="line"></div>

        <div style="margin-top: 1.4rem">
          <span>{{ $t("index.text128") }}</span>
          <span style="color: #d12c21; font-weight: bold"
            >{{ orderInfo.price }}HF</span
          >
        </div>

        <div style="margin-top: 1.4rem">
          <span>{{ $t("index.text129") }}</span>
          <span style="font-weight: bold">X{{ orderInfo.quantity }}</span>
        </div>

        <div style="margin-top: 1.4rem">
          <span>{{ $t("index.text35") }}</span>
          <span style="color: #d12c21; font-weight: bold"
            >{{ orderInfo.shipping }}HF</span
          >
        </div>

        <div style="margin-top: 1.4rem" class="jcsb">
          <div></div>
          <div>
            <span style="font-weight: bold">{{ $t("index.text130") }}</span>
            <span
              style="
                color: #d12c21;
                font-weight: bold;
                font-size: 2rem;
                margin-left: 0.5rem;
              "
              >{{ orderInfo.total }}HF</span
            >
          </div>
        </div>
      </div>

      <div class="orderInfo">
        <div class="jcsb">
          <span style="color: #666666">{{ $t("index.text131") }}</span>
          <span style="font-weight: bold">{{ orderInfo.orderNo }}</span>
        </div>

        <div class="jcsb mt2r">
          <span style="color: #666666">{{ $t("index.text132") }}</span>
          <span style="font-weight: bold">{{ orderInfo.createtime }}</span>
        </div>

        <div class="jcsb mt2r" v-if="orderInfo.receivetime != 0">
          <span style="color: #666666">{{ $t("index.text133") }}</span>
          <span style="font-weight: bold">{{
            conversion(orderInfo.receivetime)
          }}</span>
        </div>
      </div>
    </div>

    <div class="foot">
      <div
        class="confirm jcc"
        v-if="orderInfo.status == 0 && type == 1"
        @click="deliveryPopup = true"
      >
        {{ $t("index.text134") }}
      </div>

      <div
        @click="cancelOrder"
        class="cancel jcc"
        v-if="type == 0 && (orderInfo.status == 0 || orderInfo.status == 1)"
      >
        {{ $t("index.text135") }}
      </div>
      <div
        @click="confirmReceipt"
        class="confirm jcc"
        v-if="type == 0 && orderInfo.status == 1"
      >
        {{ $t("index.text136") }}
      </div>
      <div
        class="confirm jcc"
        v-if="
          (orderInfo.is_evaluate != 1 && orderInfo.status == 2) ||
            orderInfo.status == 3
        "
        @click="evaluatePopup = true"
      >
        {{ $t("index.text137") }}
      </div>
    </div>

    <van-popup v-model="evaluatePopup" class="evaluatePopup">
      <div class="content">
        <img
          src="../../assets/img/close.png"
          class="close"
          alt=""
          @click="evaluatePopup = false"
        />
        <div class="title">{{ $t("index.text138") }}</div>

        <img src="../../assets/img/pic6.png" class="pic6" alt="" />
        <img src="../../assets/img/pic7.png" class="pic7" alt="" />
        <div class="evaluateBox jcsb">
          <div
            class="evaluateItem jcc"
            @click="evaluateIndex = 1"
            :class="{ evaluateItemActive: evaluateIndex == 1 }"
          >
            <img src="../../assets/img/pic8.png" alt="" />
            <span>{{ $t("index.text139") }}</span>
          </div>

          <div
            class="evaluateItem jcc"
            @click="evaluateIndex = 2"
            :class="{ evaluateItemActive: evaluateIndex == 2 }"
          >
            <img src="../../assets/img/pic9.png" alt="" />
            <span>{{ $t("index.text140") }}</span>
          </div>

          <div
            class="evaluateItem jcc"
            @click="evaluateIndex = 3"
            :class="{ evaluateItemActive: evaluateIndex == 3 }"
          >
            <img src="../../assets/img/pic10.png" alt="" />
            <span>{{ $t("index.text141") }}</span>
          </div>
        </div>
        <div class="confirm jcc" @click="confirmEvaluation">
          {{ $t("index.text10") }}
        </div>
      </div>
    </van-popup>

    <van-popup v-model="deliveryPopup" class="deliveryPopup">
      <div class="content">
        <img
          src="../../assets/img/close.png"
          class="close"
          alt=""
          @click="deliveryPopup = false"
        />
        <div class="title">{{ $t("index.text142") }}</div>

        <img src="../../assets/img/pic6.png" class="pic6" alt="" />
        <img src="../../assets/img/pic7.png" class="pic7" alt="" />
        <div class="contentBox">
          <div class="receivingAddress">
            <div
              class="receivingAddressTitle"
              @click="$router.push('/harvestAddress')"
            >
              <div class="receivingAddressText mt2r">
                {{ $t("index.text143") }}
              </div>
            </div>

            <div class="userInfo mt2r jcsb">
              <span>{{
                orderInfo.address_text && orderInfo.address_text.name
              }}</span>
              <span>{{
                orderInfo.address_text && orderInfo.address_text.phone
              }}</span>
            </div>

            <div class="userAddress mt2r">
              {{ orderInfo.address_text && orderInfo.address_text.address }}
            </div>

            <div class="line"></div>

            <textarea
              class="textarea"
              type="textarea"
              :placeholder="$t('index.text144')"
              v-model.trim="expressNumber"
              rows="7"
            ></textarea>
          </div>
          <div class="confirm jcc" @click="delivery">
            {{ $t("index.text142") }}
          </div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  components: {},

  data() {
    return {
      evaluatePopup: false,
      evaluateIndex: 1,
      orderId: undefined,
      orderInfo: {},
      type: undefined,
      deliveryPopup: false,
      expressNumber: ""
    };
  },
  async activated() {
    this.orderId = this.$route.query.orderId;
    this.type = this.$route.query.type;

    this.getOrderDetails(this.orderId);
  },

  methods: {
    conversion(t) {
      let timeStamp = t;
      let dateTime = new Date(timeStamp * 1000);
      let year = dateTime.getFullYear();
      let month =
        dateTime.getMonth() + 1 < 10
          ? "0" + (dateTime.getMonth() + 1)
          : dateTime.getMonth() + 1;
      let day =
        dateTime.getDate() < 10 ? "0" + dateTime.getDate() : dateTime.getDate();

      let hour =
        dateTime.getHours() < 10
          ? "0" + dateTime.getHours()
          : dateTime.getHours();
      let minutes =
        dateTime.getMinutes() < 10
          ? "0" + dateTime.getMinutes()
          : dateTime.getMinutes();
      let second =
        dateTime.getSeconds() < 10
          ? "0" + dateTime.getSeconds()
          : dateTime.getSeconds();

      let time =
        year +
        "-" +
        month +
        "-" +
        day +
        " " +
        hour +
        ":" +
        minutes +
        ":" +
        second;

      return time;
    },
    getOrderDetails(id) {
      this.$api
        .get("orders/edit", {
          id: id
        })
        .then(res => {
          this.orderInfo = res.data;
        });
    },
    cancelOrder() {
      this.$dialog
        .confirm({
          title: this.$t("index.text145")
        })
        .then(() => {
          this.$api
            .post("orders/status", {
              status: 3,
              id: this.orderId,
              type: 0
            })
            .then(res => {
              if (res.code != 200) {
                this.$toast(res.msg);
                return;
              } else {
                this.$toast(this.$t("index.text146"));
                this.$router.push("/myOrder");
              }
            });
        })
        .catch(() => {});
    },

    confirmReceipt() {
      this.$dialog
        .confirm({
          title: this.$t("index.text147")
        })
        .then(async () => {
          let gasPrice = await this.getGasPrice();

          let gas = await this.core
            .hfContract()
            .transfer(
              this.orderInfo.shopInfo.address,
              this.toWei(this.orderInfo.total)
            )
            .estimateGas();
          await this.core
            .hfContract()
            .transfer(
              this.orderInfo.shopInfo.address,
              this.toWei(this.orderInfo.total)
            )
            .send({
              from: this.userAddress,
              gas: gas + 5000,
              gasPrice: gasPrice
            });

          this.$api
            .post("orders/status", {
              status: 2,
              id: this.orderId
            })
            .then(res => {
              if (res.code != 200) {
                this.$toast(res.msg);
                return;
              } else {
                this.$toast(this.$t("index.text148"));
                this.evaluatePopup = true;
                this.getOrderDetails(this.orderId);
              }
            });
        })
        .catch(() => {});
    },

    delivery() {
      if (!this.expressNumber) {
        this.$toast(this.$t("index.text144"));
        return;
      }
      this.$dialog
        .confirm({
          title: this.$t("index.text149")
        })
        .then(() => {
          this.$api
            .post("orders/status", {
              status: 1,
              id: this.orderId,
              shippingNo: this.expressNumber,
              type: 1
            })
            .then(res => {
              if (res.code != 200) {
                this.$toast(res.msg);
                return;
              } else {
                this.$toast(this.$t("index.text150"));
                this.deliveryPopup = false;
                this.expressNumber = "";
                this.getOrderDetails(this.orderId);
              }
            });
        })
        .catch(() => {});
    },
    confirmEvaluation() {
      this.$api
        .post("orders/evaluate", {
          evaluate: this.evaluateIndex,
          id: this.orderId
        })
        .then(res => {
          if (res.code != 200) {
            this.$toast(res.msg);
            return;
          } else {
            this.$toast(this.$t("index.text151"));
            this.evaluatePopup = false;
            this.getOrderDetails(this.orderId);
          }
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.orderDetails {
  background: #f7f7f7;

  display: flex;
  flex-direction: column;
  height: 100%;
}

.orderDetailsHeader {
  padding: 0 2rem;
  background: #d12c21;
  position: fixed;
  top: 0;
  width: 100%;
  height: 5rem;
  z-index: 99;

  .headerContent {
    display: flex;
    align-items: center;
    position: relative;
    height: 5rem;
  }
  .van-icon {
    font-size: 1.8rem;
    color: #fff;
  }

  span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    font-size: 1.6rem;
  }
}
.orderDetailsMainHeader {
  background: #bd303e;
  height: 37.5rem;
}
.orderDetailsMain {
  flex: 1;
  overflow-x: hidden;
  padding: 2rem 1.6rem;

  .state {
    .stateLeft {
      font-size: 1.6rem;
      font-weight: bold;
    }
    .stateRight {
      padding: 1rem 1.5rem;
      height: 3rem;
      background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
      border-radius: 1.5rem;
      color: #fff;
    }
  }

  .logisticsNum {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1.5rem;
    background: #ffffff;
    border-radius: 0.8rem;
    .num {
      img {
        width: 1.6rem;
        height: 1.6rem;
        margin-right: 0.5rem;
      }
    }
  }

  .receivingAddress {
    background: #ffffff;
    border-radius: 0.8rem;
    padding: 1.5rem;

    .userInfo {
      span {
        font-weight: bold;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .navBar2 {
        width: 1.3rem;
        height: 1.3rem;
        margin-right: 0.3rem;
      }
    }
    .userAddress {
      line-height: 2.8rem;
    }
  }

  .orderItem {
    margin-top: 1.4rem;
    padding: 1.4rem;
    background: #fff;
    border-radius: 0.8rem;

    .orderNum {
      font-size: 1.2rem;
    }

    .orderContent {
      margin: 1.4rem 0;
      padding: 1rem;
      background: #f7f7f7;
      display: flex;
      .left {
        width: 8.5rem;
        height: 8.5rem;
      }

      .right {
        margin-left: 2rem;
        flex: 1;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        .textInfo {
          font-weight: bold;
          font-size: 1.2rem;
          line-height: 2.5rem;
        }
        .price {
          font-size: 1.8rem;
          color: #d12c21;
          font-weight: bold;
        }
      }
    }

    .line {
      background: #cccccc;
      height: 0.1rem;
    }
  }

  .orderInfo {
    margin-top: 1.4rem;
    padding: 1.4rem;
    background: #fff;
    border-radius: 0.8rem;
  }
}

.foot {
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background: #fff;
  padding: 0 2rem;
  .cancel {
    height: 4rem;
    border-radius: 2rem;
    border: 0.2rem solid #666666;
    padding: 0 2rem;
    color: #666666;
    margin-right: 2.5rem;
  }

  .confirm {
    padding: 0 2rem;
    height: 4rem;
    border-radius: 2rem;
    background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
    box-shadow: 2px 5px 20px 1px rgba(255, 16, 0, 0.28);
    color: #fff;
  }
}

.evaluatePopup {
  border-radius: 0.5rem;
  width: 90vw;
  background: #fcefd3;
  .content {
    position: relative;
    padding: 2rem;
  }
  .close {
    position: absolute;
    top: 1.3rem;
    right: 2rem;
    width: 3rem;
    height: 3rem;
  }
  .title {
    text-align: center;
    font-size: 1.6rem;
    font-weight: bold;
  }
  .pic6 {
    position: absolute;
    right: 0;
    bottom: 3.3rem;
    width: 5rem;
    height: 3rem;
  }

  .pic7 {
    position: absolute;
    top: 2rem;
    left: 0;
    width: 5rem;
    height: 2.5rem;
  }
  .evaluateBox {
    margin-top: 3rem;
    .evaluateItem {
      width: 8.75rem;
      height: 5rem;
      background: #cccccc;
      border-radius: 0.4rem;
      img {
        width: 2.1rem;
        height: 2.1rem;
      }
      span {
        font-weight: bold;
        color: #fff;
        margin-left: 0.5rem;
        font-size: 1.6rem;
      }
    }

    .evaluateItemActive {
      background: #d12c21;
    }
  }

  .confirm {
    position: relative;
    z-index: 9;
    margin-top: 5rem;
    height: 5rem;
    background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
    box-shadow: 2px 5px 20px 1px rgba(255, 16, 0, 0.28);
    border-radius: 0.8rem;
    font-size: 1.6rem;
    color: #fff;
    font-weight: bold;
  }
}

.deliveryPopup {
  border-radius: 0.5rem;
  width: 90vw;
  background: #fcefd3;
  .tips {
    text-align: center;
    line-height: 2.8rem;
  }
  .content {
    position: relative;
    padding: 2rem;
  }
  .close {
    position: absolute;
    top: 1.3rem;
    right: 2rem;
    width: 3rem;
    height: 3rem;
  }
  .title {
    text-align: center;
    font-size: 1.6rem;
    font-weight: bold;
  }
  .pic6 {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    width: 5rem;
    height: 3rem;
  }

  .creditScorePopupPic6 {
    position: absolute;
    right: 0;
    bottom: 0.5rem;
    width: 6rem;
    height: 4rem;
  }

  .pic7 {
    position: absolute;
    top: 2rem;
    left: 0;
    width: 6rem;
    height: 2.5rem;
  }
  .contentBox {
    .receivingAddress {
      .receivingAddressTitle {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .receivingAddressText {
          display: flex;
          align-items: center;
          font-weight: bold;
        }
      }

      .userInfo {
        span {
          font-weight: bold;
        }
      }

      .userAddress {
        line-height: 2.5rem;
      }

      .line {
        margin: 2.5rem 0;
        background: #cececc;
        height: 0.1rem;
      }

      .textarea {
        position: relative;
        z-index: 9;
        width: 100%;
        border: 0;
        padding: 1.4rem 0 0 1.6rem;
        box-sizing: border-box;
        border-radius: 1rem;
      }

      .textarea::placeholder {
        color: #000;
      }
    }
    .confirm {
      position: relative;
      z-index: 9;
      margin-top: 2rem;
      height: 5rem;
      background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
      box-shadow: 2px 5px 20px 1px rgba(255, 16, 0, 0.28);
      border-radius: 0.8rem;
      font-size: 1.6rem;
      color: #fff;
      font-weight: bold;
    }
  }
}
</style>



